.signupContainer{
    background-color: #f6f8f9;
    border-radius: 3px;
    box-shadow: 0 20px 30px 0 rgba(36,50,66,.14);
    margin-left: auto;
    margin-right: auto;
    margin-top: 80px;
    padding: 32px 60px;
    position: relative;
    width: 90%;
    max-width: 676px;
    
}
.signupContainer label span{
    color: red;
    vertical-align: middle;
    margin-left: 3px;

}
.signup{
    /* padding-left: 80px;
    padding-right: 80px; */
    padding-top: 48px;
}
.signupContainer h1{
    font-weight: 600;
    text-align: center;
}
.inputGroup{
margin-bottom: 24px;
}
.signupContainer p a{
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    text-decoration: none;
    color: #796eff;
    padding-left: 5px;

}
.signupContainer p a:hover{
    border-bottom: 2px solid #635ac7;
    color: #635ac7;
    text-decoration: none;

}
.error{
    color: red;
}