  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #646f79;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  width: 100%;
  margin: 0 auto;
  float: none;
  max-width: 1200px;
  padding: 0 15px;
}
h1, h2, h3, h4, h5, h6{
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 15px;
  color: #151b26;
}
h3{
  font-size: 45px;
}
p{
  margin-bottom: 15px;
  font-size: 16px;
}

ul{
  padding: 0;
  margin: 20px 0;
  list-style: none;
  text-align: center;
}
ul li{
  display: block;
  margin-bottom: 15px;
  text-align: left;
}
ul li a{
  text-decoration: none;
  font-size: 18px;
  /* text-transform: uppercase; */
}
/* ul li:not(:first-child){
  margin-left: 15px;
} */
label{
  font-size: 16px;
  display: block;
  margin-bottom: 8px;
}
input{
  background-color: #fff;
  border: 2px solid #e0e6e8;
  border-radius: 3px;
  height: 50px;
  line-height: normal;
  margin: 0;
  padding: 0 0 0 16px;
  -webkit-transition: border .15s;
  transition: border .15s;
  width: 100%;
  font-size: 16px;
  box-sizing: border-box;
  color: #66717b;

}
input:focus{
  border: 2px solid #635ac7;
  outline: none;
}
input[type="submit"]{
  background-color: #796eff;
  color: #fff;
  cursor: pointer;
  border: none;
  margin-bottom: 32px;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
input[type="submit"]:hover{
  background: #635ac7;

}
button[type="button"]{
  height: 50px;
  margin-bottom: 20px;
  display: block;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 2px 0px, rgba(0, 0, 0, 0.24) 0px 0px 1px 0px !important;
  border-radius: 3px !important;
}
button[type="button"]>div{
  display: none;

}
button[type="button"] span{
  text-align: center;
    margin: 0 auto;
}
